#wooconvo-front-wrapper {
  background-color: white;
}
.customer {
  padding: 10px;
  margin: 20px;
  background-color: white;
}
.reply {
  padding: 10px;
  margin: 20px;
  background-color: white;
}

.preview-thumb {
  background: #fff;
  border-radius: 20px;
  display: inline-block;
  height: 175px;
  margin: 1rem;
  position: relative;
  width: 175px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  text-align: center;
}

.preview-thumb-img {
  object-fit: contain;
}
.preview-thumb-tool {
  padding: 0;
  margin: 0;
  text-align: center;
}

.preview-thumb-upload {
  background: rgba(255, 255, 255, 0);
  border-radius: 20px;
  display: inline-block;
  height: 90px;
  padding-top: 5px;
  margin-left: 10px;
  width: auto;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.preview-thumb-img-upload {
  object-fit: contain;
}
.preview-thumb-tool-upload {
  padding: 0;
  margin: 0;
  text-align: center;
}
